"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardFooter,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Tabs, TabsContent } from "@/components/ui/tabs";
import { Textarea } from "@/components/ui/textarea";
import { CalendarIcon, Clock, User } from "lucide-react";

const upcomingAppointments = [
  {
    id: 1,
    date: "2023-06-15",
    time: "10:00 AM",
    doctor: "Dr. Smith",
    reason: "Annual checkup",
  },
  // Add more appointments as needed
];

export default function Component() {
  const [notes, setNotes] = useState<{ [key: number]: string }>({});

  const handleReschedule = (appointment: any) => {
    // Implement rescheduling logic
    console.log("Rescheduling appointment:", appointment);
  };

  const handleCancel = (id: number) => {
    // Implement cancellation logic
    console.log("Cancelling appointment:", id);
  };

  const handleConfirm = (id: number) => {
    // Implement confirmation logic
    console.log("Confirming appointment:", id, "with notes:", notes[id]);
  };

  return (
    <Tabs defaultValue="upcoming">
      <TabsContent value="upcoming">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
          {upcomingAppointments.map((appointment) => (
            <Card
              key={appointment.id}
              className="hover:shadow-lg transition-shadow duration-300"
            >
              <CardHeader className="bg-blue-50 dark:bg-gray-700">
                <CardTitle className="flex items-center">
                  <CalendarIcon className="mr-2 h-5 w-5 text-blue-500" />
                  {appointment.date}
                </CardTitle>
                <CardDescription className="flex items-center">
                  <Clock className="mr-2 h-4 w-4 text-gray-500 dark:text-gray-400" />
                  {appointment.time}
                </CardDescription>
              </CardHeader>
              <CardContent className="pt-4">
                <div className="flex items-center mb-2">
                  <User className="mr-2 h-4 w-4 text-gray-500 dark:text-gray-400" />
                  <p className="font-semibold text-gray-900 dark:text-white">
                    {appointment.doctor}
                  </p>
                </div>
                <p className="text-sm text-gray-500 dark:text-gray-400 mb-4">
                  {appointment.reason}
                </p>
                <Textarea
                  placeholder="Additional notes"
                  value={notes[appointment.id] || ""}
                  onChange={(e) =>
                    setNotes({ ...notes, [appointment.id]: e.target.value })
                  }
                  className="w-full mb-4"
                />
              </CardContent>
              <CardFooter className="flex justify-between">
                <Button
                  variant="outline"
                  className="border-gray-200 dark:border-gray-700"
                  onClick={() => handleReschedule(appointment)}
                >
                  Reschedule
                </Button>
                <Button
                  variant="destructive"
                  onClick={() => handleCancel(appointment.id)}
                >
                  Cancel
                </Button>
                <Button
                  variant="default"
                  onClick={() => handleConfirm(appointment.id)}
                >
                  Confirm
                </Button>
              </CardFooter>
            </Card>
          ))}
        </div>
      </TabsContent>
    </Tabs>
  );
}
